import React from "react";
import "../Layout.css";

function Fangstrapport() {
  return (
    <div className="rules-container">
      <div className="outer-card">
        <h1>Fngstrapporter Sbylundssjn</h1>
        <p>
          Hr kan du se medlemmarnas rapporter frn Sbylundssjn via Ifiske.
        </p>
        <iframe
          src="https://www.ifiske.se/fisketips-sabylundssjon.htm"
          title="Fngstrapporter Ifiske"
          style={{
            width: "100%",
            height: "800px",
            border: "none",
            borderRadius: "12px",
            minHeight: "500px",
          }}
        ></iframe>
      </div>
    </div>
  );
}

export default Fangstrapport;
